html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo img {
  vertical-align: top;
  width: 40px;
  height: 40px;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  margin-right: 20px;
  font-weight: 500;
}

nav ul li:last-child {
  margin-right: 0;
}

nav ul li a {
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

nav ul li a:hover {
  color: #00acdc;
}

nav ul li .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

nav ul li a.is-active {
  color: #00acdc;
}
